import React, {  Component } from 'react'
import {
    // CBadge,
    CButton,
    // CButtonGroup,
    CCard,
    CCardBody,
    // CCardFooter,
    CCardHeader,
    CCol,
    // CProgress,
    CRow,
    // CCallout
} from '@coreui/react'

import CIcon from '@coreui/icons-react'
import "grapesjs/src/styles/scss/main.scss";
import 'grapesjs/dist/css/grapes.min.css';

import './New.css'
// import grapesjs from 'grapesjs';
// import { Editor } from 'grapesjs-react';

import Axios from '../../lib/api';


class View extends Component {
    constructor(props) {
        super(props);

        this.state = {}
        this.pageId = props.match.params._id;
        console.log(this.pageId)
    }
    componentDidMount = () => {
        Axios.get(`admin/page/pages?id=${this.pageId}`).then(r => {
            console.log(r)
            this.setState({ page: r.data[0] })
        })
    }
    onBack() {
        this.props.history.push('/dashboard');
    }
    onTitleChange(e) {
        this.setState({ title: e => e.target.value })
    }
    render() {
        var { page } = this.state;
        return (
            <>
                <CRow>
                    <CCol>
                        <CCard>
                            <CCardHeader>
                                View Page

                            </CCardHeader>
                            <CCardBody>
                                {page ? <h1>{page.title}</h1> : null}
                                <CButton type="submit" size="sm" color="primary" onClick={() => { this.onBack() }}><CIcon name="cil-scrubber" /> Back</CButton>
                                <hr/>

                                {page ? <div dangerouslySetInnerHTML={{ __html: page.html }}></div> : null}


                            </CCardBody>
                        </CCard>
                    </CCol>
                </CRow>
            </>
        )
    }
}

export default View
